<template>
    <div class="ry-p-t-10 flex-row flex-align-center">
        <template v-for="(item, idx) in data" :key="idx">
            <div class="flex-row ry-m-r-40 flex-align-center" @click="emits('update:modelValue', item.value)">
                <img class="ry-w-28 ry-h-28"
                    :src="modelValue == item.value ? require('@/assets/partner/select.png') : require('@/assets/partner/unselect.png')"
                    alt="">
                <span class="ry-p-l-10">{{ item.title }}</span>
            </div>
        </template>
    </div>
</template>

<script lang="ts" setup>
import { ref } from 'vue';
withDefaults(defineProps<{
    modelValue: any
    data: Array<{
        title: string,
        value: any
    }>
}>(), {
    data: () => [],
    modelValue: ''
})
const emits = defineEmits(['update:modelValue'])


</script>


